.Trash {
  height: var(--icon);
  width: var(--icon);
  overflow: hidden;
  display: grid;
  -ms-flex-line-pack: center;
      align-content: center;
  grid-template-columns: 1;
  grid-template-rows: 1;
  border-left: 1px solid black;
  padding-left: 5px;
  margin-left: 5px;
}

.Trash img {
  grid-column: 1 / -1;
  grid-row: 1 / -1;
  width: 100%;
  -o-object-fit: cover;
     object-fit: cover;
}



